@import '~node_modules/modern-normalize/modern-normalize.css';

body {
    justify-content: center;
    text-align: center;
}

img {
    display: block;
    width: 100%;
    height: 250px;
    margin: 0;
    padding: 0;
    object-fit: cover;
}

b {
    margin-top: 5;
}

p {
    margin-top: 5;
    margin-bottom: 10;
}

.search-form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

.photo-card {
    flex-basis: calc((100% - 4 * 20px) / 4);
    margin-top: 20px;
    margin-left: 20px;
    cursor: pointer;
}

.gallery-list{
    display: flex;
    flex-wrap: wrap;
    margin-top: -20px;
    margin-left: -20px;
    margin-bottom: 20px;
    padding: 0 10px;

}

.info{
    display: flex;
    justify-content: center;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.info-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-family: sans-serif;
    font-size: 17px;
}

.load-more{
    cursor: pointer;
    margin-top: 20px;
    margin-bottom: 50px;
    width: 130px;
    height: 30px;
}

.is-disabled{
    display: none;
}





